@mixin button-reset {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
}

@mixin list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
  border: none;
}

@mixin title {
  padding: 0;
  margin: 0;
  text-transform: uppercase;
}
